import React, { useState } from 'react';
import CancelIcon from '@material-ui/icons/Cancel';

const App=()=>{

  const[inputlist, setinputlist]=useState("");
  const[itemsa, setitems]=useState([]);
  
  const items=(event)=>{ 
    setinputlist(event.target.value);
  };

  const clicks=()=>{
    setitems((olditems)=>{
      return[...olditems,inputlist];
    });
    setinputlist("");
  };

  const deleteitems=(id)=>{
    setitems((olditems)=>{
      return olditems.filter((arrElem, index)=>{return(index !== id)})
    })
  };
 

const ToDoList=(props)=>{
 
  <button onClick={()=>{props.onSelect(props.id);}}><CancelIcon/></button>
}



  return(
  <>
    <div className="maindiv">
    <div className="div">
      <h1>ToDo list</h1>
      <div className="input">
      <input type='text' placeholder="Add item hear" onChange={items} value={inputlist}></input>
      <button onClick={clicks}>+</button>
      </div>
      <ol>
        {itemsa.map( (itemval, index)=>{return(<>
         <ToDoList 
        key={index}
        id={index}
        text={itemval}
        onselect={deleteitems}
        /> 
        <li>{itemval}</li></>)})}
      </ol>
    </div>
  </div>
</>
)

};


export default App;